* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
  background: url(/clickyGame/static/media/bg.9946fa27.jpg);
  color: white;
  font-family: monospace;
}

.form {
    margin: 20px;
  }
  
  .form input {
    display: block;
    margin-top: 5px;
    margin-bottom: 5px;
    width: 100%;
  }
.card {
    background: #fff;
    border-radius: 2px;
    height: 120px;
    margin: 1rem;
    position: relative;
    width: 120px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
    cursor: pointer;
  }
  
  .card:hover {
    border: 7px solid black;
  }
  
  .card > img {
    width: 120px;
    height: 120px;
  }
.wrapper {
    padding-top: 50px;
    background: rgba(0,20,0,.3);
    height: 100%;
    width: 80%;
    margin: 10px auto;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    padding: 20px;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-content: flex-start;
            align-content: flex-start;
    overflow: auto;
  }
